@import url('https://fonts.googleapis.com/css?family=Montserrat:500');

.item {
    height: 100%;
    align-self: center!important;
    display: flex!important;
}

header {
    display: flex!important;
    height: 70px;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    position: fixed;
    background: white;
    z-index: 2020;
}



.logo1 {
    height: 60px;
    width: 60px;
    cursor: pointer;
    margin-right: auto;
    margin-left: 20px;
    
}

.header_nav {
    width: 85%;
    display: flex!important;
    justify-content: inherit;
    
}

.nav__links {
    width: 30%;
    padding-right: 10px;
    justify-content: flex-end;
    display: flex!important;
}


.nav_items {
    display: inline;
    padding: 10px!important;
}


.cta {
    width: 80px;
    height: 40px;
    padding: 5px 5px!important;
    background-color: white;
    border: 2px solid rgba(50,50,50,0.7);
    border-radius: 4px;
    cursor: pointer;
    font-weight: 300;
    font-size: 16px;
    color: rgba(50,50,50,1);
    text-decoration: none
}

.cta:hover {
    width: 80px;
    height: 40px;
    padding: 5px 5px!important;
    background-color: rgba(50,50,50,1);
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.3s ease 0s;
    font-weight: 300;
    font-size: 16px;
    color: white;
    text-decoration: none;
    transition:all .5s ease;

}

a {
    font-weight: 500;
    font-size: 8px;
    color: rgba(80,80,80,1);
    text-decoration: none 
}

.searchbar_header {
    margin: 13px;
    margin-left: 20%;
    width: 50%;
    height: 32px;
    border: 1px solid rgba(200,200,200,0.7);
    border-radius: 2px;
    display: flex!important;
}


.searchbox {
    width: calc(100%);
    place-content: center;
    box-shadow:inset 0 1px 2px gainsboro;
    padding: 4px;
    line-height: 24px;
}

.searchbox:focus {
    width: calc(100%);
    place-content: center;
    box-shadow:inset 3px 3px 2px gainsboro;
    padding: 4px;
    line-height: 24px;
}

.search_button {
    width: 70px;
    height: 32px;
    border-left: 1px solid rgba(200,200,200,0.7);
    background-color: whitesmoke;
    cursor: pointer;
    transition: all 0.3s ease 0s;
}

.search_button:hover {
    width: 70px;
    height: 32px;
    border-left: 1px solid rgba(200,200,200,0.7);
    background-color: gainsboro;
    box-shadow:inset 0 1px 2px gainsboro;
    cursor: pointer;
    transition:all .5s ease;
}

.search_img {
    width: 15px;
    height: 15px;
}